@import 'mixins.scss';

.sectionWorkspace {
    margin: 0 0 80px 0;

    .sectionInner {
        display        : flex;
        flex-direction : column;
        align-items    : flex-start;
        justify-content: space-between;
        padding: 0 143px;
    }

    &_title {
        font-family: "Raleway", sans-serif;
        font-style : normal;
        font-weight: 400;
        font-size  : toRem(55);
        color      : $primary_color;
        margin     : 0 0 40px 0;
    }
    &_text{
        font-family: "Raleway", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: toRem(22);
        line-height: 1.6;
        margin: 0 0 40px 0;
    }

    &_list {
        width     : 100%;
        list-style: none;
        height    : 100%;

        &Item {
            display        : flex;
            flex-direction : row;
            align-items    : flex-start;
            justify-content: flex-start;
            margin         : 0 0 20px 0;
            font-family    : "Raleway", sans-serif;
            font-style     : normal;
            font-weight    : 400;
            font-size      : toRem(19);
            line-height    : 140%;

            &::before {
                display    : block;
                content    : '';
                background : transparent url(../img/svg/listDisk.svg) 50% 50% no-repeat;
                width      : 14px;
                height     : 14px;
                margin     : 0 20px 0 0;
                flex-shrink: 0;
            }
        }

    }
}