@import 'mixins.scss';

.sectionHeader_burger {
  position       : relative;
  top            : 0;
  z-index        : 100;
  display        : flex;
  align-items    : center;
  justify-content: center;

  .burger {
    margin-top: toRem(20);
    width     : toRem(100);
    height    : auto;
    border    : none;
  }

  .burger-menu_button {
    display           : block;
    position          : absolute;
    top               : 0;
    right             : 0;
    z-index           : 100;
    width             : toRem(32);
    height            : toRem(32);
    -webkit-transition: 0.4s;
    -moz-transition   : 0.4s;
    -o-transition     : 0.4s;
    transition        : 0.4s;


    background-color: $secondary_color;
    width           : 40px;
    height          : 40px;
    position        : relative;
    cursor          : pointer;

    &::after,
    &::before {
      content         : '';
      width           : 22px;
      height          : 2px;
      background-color: #fff;
      position        : absolute;
      top             : 15px;
      left            : 9px;
    }

    &::after {
      top   : auto;
      bottom: 15px;
      width : 11px;
    }

    background-color: $secondary_color;
    width           : 40px;
    height          : 40px;
    position        : relative;
    cursor          : pointer;

    &::after,
    &::before {
      transition      : all 0.5s;
      content         : '';
      width           : 22px;
      height          : 2px;
      background-color: #fff;
      position        : absolute;
      top             : 15px;
      left            : 9px;
    }

    &::after {
      top   : auto;
      bottom: 15px;
      width : 11px;
    }

    &_active {
      // right: toRem(75);  
    }
  }

  .burger-menu_button:hover .burger-menu_lines {
    filter: brightness(0.7);
  }

  .burger-menu_button:hover {

    .burger-menu_lines::before,
    .burger-menu_lines::after,
    .burger-menu_lines {
      background-color: rgba(0, 0, 0, 0.7);
    }
  }

  .burger-menu_button.burger-menu_button_active:hover {
    .burger-menu_lines {
      background-color: transparent;
    }
  }





  .burger-menu_active .burger-menu_button::before {
    top      : 19px;
    transform: rotate(45deg);
  }

  .burger-menu_active .burger-menu_button::after {
    bottom   : 19px;
    width    : 22px;
    transform: rotate(-45deg);
  }

  .burger-menu_nav {
    padding           : 60px 30px 20px 30px;
    position          : absolute;
    top               : 0;
    right             : -15px;
    z-index           : 70;
    display           : flex;
    flex-flow         : column;
    background-color  : #fff;
    overflow-y        : auto;
    height            : 372px;
    width             : 323px;
    top               : -400px;
    -webkit-transition: 0.8s;
    -moz-transition   : 0.8s;
    -o-transition     : 0.8s;
    transition        : 0.8s;

    &-img {
      width : 160px;
      height: 30px;
      margin: 0 0 20px 0;
    }

    .sectionHeader_lang,
    .socialLogosWrapper,
    .sectionHeader_btnWrapper,
    .sectionHeader_contacts-phone {
      display: none;
    }
  }

  .burger-menu_active .burger-menu_nav {
    top               : -55px;
    -webkit-transition: 0.4s;
    -moz-transition   : 0.4s;
    -o-transition     : 0.4s;
    transition        : 0.4s;
  }

  .burger-menu_link {
    font-family    : Raleway, sans-serif;
    font-style     : normal;
    font-weight    : 400;
    font-size      : toRem(22);
    line-height    : 120%;
    text-decoration: none;
    letter-spacing : 0.1em;
    color          : #000;
    width          : 100%;
    height         : toRem(50);
    display        : flex;
    justify-content: flex-start;
    align-items    : center;
    text-transform : uppercase;
  }



  .burger-menu_link:hover {
    color: $primary_color;
  }

  .burger-menu_overlay {
    display : none;
    position: fixed;
    top     : 0;
    left    : 0;
    width   : 100vw;
    height  : 100vh;
    z-index : 50;
  }

  .burger-menu_active .burger-menu_overlay {
    display         : block;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .logo {
    text-align: center;

    .logoImg {
      height: toRem(40);
    }
  }
}