@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Bold.ttf") format("truetype"), url("../fonts/Raleway-Bold.woff") format("woff"), url("../fonts/Raleway-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Regular.ttf") format("truetype"), url("../fonts/Raleway-Regular.woff") format("woff"), url("../fonts/Raleway-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Numans";
  src: url("../fonts/Numans-Regular.ttf") format("truetype"), url("../fonts/Numans-Regular.woff") format("woff"), url("../fonts/Numans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Semibold.ttf") format("truetype"), url("../fonts/Raleway-Semibold.woff") format("woff"), url("../fonts/Raleway-Semibold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #e5e5e5;
}

.sectionInner {
  width: 1120px;
  margin: 0 auto;
}
.sectionInner * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.btn {
  background-color: #BB7A39;
  color: #fff;
  text-transform: uppercase;
  font-family: "Raleway", sans-serif;
  font-style: normal;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  outline: none;
  font-weight: bold;
  cursor: pointer;
}
.btn:hover {
  color: #cccccc;
}

.sectionHeader {
  width: 100%;
  height: 80px;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
}
.sectionHeader .sectionInner {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_Logo {
  width: 213px;
  height: 40px;
}
.sectionHeader_btnWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 380px;
}
.sectionHeader_btnWrapper-btn {
  font-family: "Raleway", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 1rem;
  width: 180px;
  height: 40px;
  border: 1px solid #CEDCDE;
  outline: none;
  background-color: #fff;
  text-transform: uppercase;
  color: #000;
  transition: all 0.4s;
  cursor: pointer;
}
.sectionHeader_btnWrapper-btn:hover {
  background-color: #BB7A39;
  border: 1px solid #BB7A39;
  color: #fff;
}
.sectionHeader_btnWrapper-btn.active {
  background-color: #BB7A39;
  border: 1px solid #BB7A39;
  color: #fff;
}
.sectionHeader_contacts {
  font-family: "Numans", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.25rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 260px;
}
.sectionHeader_contacts-phone {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
.sectionHeader_contacts-phone:active, .sectionHeader_contacts-phone:visited {
  color: inherit;
}
.sectionHeader_contacts-socialLogo {
  width: 26px;
  height: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(4, 40, 52, 0.3);
  transition: all 0.4s;
  cursor: pointer;
}
.sectionHeader_contacts-socialLogo:first-child {
  width: 500px;
}
.sectionHeader_contacts-socialLogo:hover {
  background-color: #BB7A39;
}
.sectionHeader_lang {
  width: 65px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.sectionHeader_lang-item {
  font-family: Raleway, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 0.875rem;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  opacity: 0.5;
}
.sectionHeader_lang-item:active, .sectionHeader_lang-item:visited {
  color: inherit;
}
.sectionHeader_lang-item:hover {
  color: #000;
}
.sectionHeader_lang-item.active {
  opacity: 1;
}
.sectionHeader_burger {
  position: relative;
  top: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
}
.sectionHeader_burger .burger {
  margin-top: 1.25rem;
  width: 6.25rem;
  height: auto;
  border: none;
}
.sectionHeader_burger .burger-menu_button {
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
  width: 2rem;
  height: 2rem;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  background-color: #BB7A39;
  width: 40px;
  height: 40px;
  position: relative;
  cursor: pointer;
  background-color: #BB7A39;
  width: 40px;
  height: 40px;
  position: relative;
  cursor: pointer;
}
.sectionHeader_burger .burger-menu_button::after, .sectionHeader_burger .burger-menu_button::before {
  content: "";
  width: 22px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 15px;
  left: 9px;
}
.sectionHeader_burger .burger-menu_button::after {
  top: auto;
  bottom: 15px;
  width: 11px;
}
.sectionHeader_burger .burger-menu_button::after, .sectionHeader_burger .burger-menu_button::before {
  transition: all 0.5s;
  content: "";
  width: 22px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  top: 15px;
  left: 9px;
}
.sectionHeader_burger .burger-menu_button::after {
  top: auto;
  bottom: 15px;
  width: 11px;
}
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines {
  filter: brightness(0.7);
}
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines::before,
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines::after,
.sectionHeader_burger .burger-menu_button:hover .burger-menu_lines {
  background-color: rgba(0, 0, 0, 0.7);
}
.sectionHeader_burger .burger-menu_button.burger-menu_button_active:hover .burger-menu_lines {
  background-color: transparent;
}
.sectionHeader_burger .burger-menu_active .burger-menu_button::before {
  top: 19px;
  transform: rotate(45deg);
}
.sectionHeader_burger .burger-menu_active .burger-menu_button::after {
  bottom: 19px;
  width: 22px;
  transform: rotate(-45deg);
}
.sectionHeader_burger .burger-menu_nav {
  padding: 60px 30px 20px 30px;
  position: absolute;
  top: 0;
  right: -15px;
  z-index: 70;
  display: flex;
  flex-flow: column;
  background-color: #fff;
  overflow-y: auto;
  height: 372px;
  width: 323px;
  top: -400px;
  -webkit-transition: 0.8s;
  -moz-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s;
}
.sectionHeader_burger .burger-menu_nav-img {
  width: 160px;
  height: 30px;
  margin: 0 0 20px 0;
}
.sectionHeader_burger .burger-menu_nav .sectionHeader_lang,
.sectionHeader_burger .burger-menu_nav .socialLogosWrapper,
.sectionHeader_burger .burger-menu_nav .sectionHeader_btnWrapper,
.sectionHeader_burger .burger-menu_nav .sectionHeader_contacts-phone {
  display: none;
}
.sectionHeader_burger .burger-menu_active .burger-menu_nav {
  top: -55px;
  -webkit-transition: 0.4s;
  -moz-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}
.sectionHeader_burger .burger-menu_link {
  font-family: Raleway, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 1.375rem;
  line-height: 120%;
  text-decoration: none;
  letter-spacing: 0.1em;
  color: #000;
  width: 100%;
  height: 3.125rem;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-transform: uppercase;
}
.sectionHeader_burger .burger-menu_link:hover {
  color: #0C4E58;
}
.sectionHeader_burger .burger-menu_overlay {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 50;
}
.sectionHeader_burger .burger-menu_active .burger-menu_overlay {
  display: block;
  background-color: rgba(0, 0, 0, 0.5);
}
.sectionHeader_burger .logo {
  text-align: center;
}
.sectionHeader_burger .logo .logoImg {
  height: 2.5rem;
}

